<template>
  <div class="nav">
    <ul>
      <li :class="toName===nav.name?'active':''"
        v-for="(nav,index) in navList"
        :key="index"
        @click="pageTo(nav.path)">
        <i :class="['iconfont',nav.iconClass]"></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      navList: [{
        path: '/',
        name: 'home',
        iconClass: 'icon-home'
      }, {
        path: '/tools/hupai',
        name: 'hupai',
        iconClass: 'icon-hu'
      }, {
        path: '/tools/clean',
        name: 'clean',
        iconClass: 'icon-clean'
      }],
      toName: ''
    }
  },
  mounted () {
    this.toName = this.$route.name
  },
  watch: {
    '$route' (to, from) {
      this.toName = to.name
    }
  }
}
</script>

<style lang="scss" scoped>
.nav {
  height: 60px;
  position: fixed;
  width: 100%;
  z-index: 10;
  top: 0;
  left: 0;
  background: rgba(26,31,33,0.5);
  ul {
    width: 1200px;
    padding-top: 10px;
    margin: 0 auto;
    overflow: hidden;
    li {
      float: left;
      line-height: 40px;
      margin: 0 40px;
      list-style: none;
      cursor: pointer;
      &:after {
        content: '';
        display: block;
        height: 2px;
        z-index: 1;
        opacity: 1;
        background: linear-gradient(to right, #E67207 0%, #EF4E7B 50%, #A166AB 100%);
        transform-origin: center;
        transform: scaleX(0);
        transition: transform .4s ease-out;
        margin-top: 3px;
      }
      &.active:after, &:hover:after {
        transform: scaleX(1);
      }
      .iconfont {
        font-size: 30px;
        color: #ffffff;
      }
    }
  }
}
</style>
